html {
    scroll-behavior: smooth;
    background-image: linear-gradient(to bottom, #ffffff, #fefeff, #fdfcff, #fcfbff, #fcf9ff);
}

.home {
    display: flex;
    flex-direction: column;
    width: 70%;
    margin: auto;
    border-radius: 40px;
    background-image: linear-gradient(to bottom, #ffffff, #ffffff, #ffffff, #ffffff, #ffffff, #ffffff, #fefeff, #fefeff, #fefdff, #fefdff, #fefcff, #fefbff);
}

@media only screen and (max-width: 1024px) {
    .home {
        width: 90%;
    }
}

@media only screen and (max-width: 512px) {
    .home {
        width: 100%;
    }
}