.work-card {
    padding: 8px;
    width: 100%; 
    /* change width percentage to accomodate more cards */
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;
}

.work-logo {
    height: 80px;
}

.work-info {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}

.company-name {
    font-size: 20px;
}

.work-dates {
    color: gray;
}

.work-desc {
    text-align: left;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 80%;
}

@media only screen and (max-width: 720px) {
    .work-card {
        width: 100%;
    }
}